import React, { Component } from "react";
import "./App.css";
//import "bootstrap/dist/css/bootstrap.css";

const PLACES = [
  { name: "Москва", zip: "101000" },
  { name: "Санкт-Петербург", zip: "191186" },
  { name: "Уфа", zip: "450005" },
  { name: "Сочи", zip: "354000" },
  { name: "Северо-Западная провинция", zip: "60606"},
];

class WeatherDisplay extends Component {
  constructor() {
    super();
    this.state = {
      weatherData: null
    };
  }
  componentDidMount() {
    const zip = this.props.zip;
    const URL = "http://api.openweathermap.org/data/2.5/weather?q=" +
      zip +
      "&appid=002cc78f76153d54159844f8a8a308aa&units=metric&lang=ru";
    fetch(URL).then(res => res.json()).then(json => {
      this.setState({ weatherData: json });
    });
  }
  render() {
    const weatherData = this.state.weatherData;
    if (!weatherData) return <div>Подождите...</div>;
    const weather = weatherData.weather[0];
    const iconUrl = "http://openweathermap.org/img/w/" + weather.icon + ".png";
    const iconTemp = "/Temp.png";
	const iconDay = "/Day.png";
	const iconNight = "/Night.png";
	const iconWind = "/Wind.png";

    return (
      <div class="top">
        <h1>
		<img src={iconUrl} alt={weatherData.description} />
          В городе {weatherData.name}</h1>
		  
		<div class="p"><p><span class="icon"><img src={iconTemp} alt={weatherData.description} />
		<span class="text">Температура сейчас: {weatherData.main.temp.toFixed()} С°</span></span></p>
		<p><span class="icon"><img src={iconDay} alt={weatherData.description} /><span class="text">
		Днём: {weatherData.main.temp_max.toFixed()} С°</span></span></p>
        <p><span class="icon"><img src={iconNight} alt={weatherData.description} /><span class="text">
		Ночью: {weatherData.main.temp_min.toFixed()} С°</span></span></p>
        <p><span class="icon"><img src={iconWind} alt={weatherData.description} /><span class="text">
		Ветер: {weatherData.wind.speed.toFixed(1)} м/с</span></span></p>
		
		
              </div>
	  </div>
    );
  }
}

class App extends Component {
  constructor() {
    super();
    this.state = {
      activePlace: 0
    };
  }
  render() {
    const activePlace = this.state.activePlace;
    return (
      <div className="App">

        {PLACES.map((place, index) => (
          <button
            key={index}
            onClick={() => {
              this.setState({ activePlace: index });
            }}
          >
            {place.name}
          </button>
        ))}

	  <div className="Images">
        <WeatherDisplay key={activePlace} zip={PLACES[activePlace].zip} />
      </div> 
      </div> 
	  );
  }
}

export default App;